    height: 1000px;
} */

.AppContent button {
    -webkit-appearance: none;
    background-color: gray;
    color: white;
    border-width: 0px;
}

.AppContent button:hover {
    opacity: 0.8;
}

.AppContent button:active {
    opacity: 0.8;
}

.AppContent textarea {
    resize: vertical;
    height: 200px;
    font-family: inherit;
    font-size: inherit;
    /* margin: 0px 4px; */
    border-color: lightgray;
    border-radius: 4px;
    border-style: solid;
    -webkit-appearance: none;
}

.AppContentFormContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px 8px;

}

.AppContentForm {
    display: flex;
    flex-direction: column;
    /* align-items: stretch; */
    align-items: stretch;
    max-width: 480px;
    width: 100%;
}

.AppContentForm label {
    padding: 4px 0px;
}

.AppContentForm div {
    padding: 8px;
    justify-content: space-between;
}

.AppContentForm input {
    flex-grow: 2;
}

.AppContentForm .AppContentFormSubmitButton {
    width: 240px;
    font-size: 16px;
    height: 36px;
    margin: 8px;
    align-self: center;
    border-radius: 8px;
}

.AppContentFormBugDescriptionField {
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.AppContentFormSelectFileField {
    display: flex;
    flex-direction: row;
    align-items: center;
    /* justify-content: space-between; */
}

.AppContentFormSelectFileField label {
    flex-grow: 2;
    text-align: left;
}

.AppContentFormSelectFileField span{
    font-size: 12px;
    color: gray
}

.AppContentFormSelectFileField button {
    /* flex-grow: 0; */
    /* width: 150px; */
    border-radius: 4px;
    height: 24px;
    padding: 0px 16px;
    margin-left: 8px;
    /* background-color: gray;
    -webkit-appearance: none; */
}


.AppContentFormTextField {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.AppContentFormTextField input {
    height: 20px;
    border-radius: 4px;
    border-style: solid;
    margin-left: 8px;
    border-width: 1px;
    border-color: lightgray;
    /* box-shadow: none; */
    /* -webkit-box-shadow: none; */
    -webkit-appearance: none;
    max-width: 200px;
    flex: 1;
}

.AppContentFormTextField select {
    max-width: 200px;
    flex: 1;
    height: 24px;
    border-radius: 4px;
    border-color: gray;
    /* background-color: none; */
    box-shadow: none;
    font-size: 14px;
}

.AppContentFormBugDescriptionField label {
    text-align: left;
}

.AppContentBottomBarText {
    font-size: 14px;
    color: gray;
    padding: 16px;
    margin-top: 24px;
}